@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');

.section {
    margin: 20px;
    align-items: center;
    font-family: 'Indie Flower', cursive;
    transition: box-shadow 300ms;
    font-size: 18px;
    display: inline-block;
    width: '100vw';
  }

.video-frame{
    float: none;
    width: '80vw';
}



@media only screen and (max-width: 800px) {
    .section {
        margin: 0px;
        width: 100%;
        overflow: hidden;
    }
}

@media only screen and (min-width: 800px) {
    .video-frame{
        float: left;
        width: 560px;
        height: 315px;
        align-items: center;
    }
}

.contents {
    color: white;
    font-size: 30px;
    padding-top: 8%;
    width: 100%;
    
}